import { View } from './base/view';
import { ensureElement } from '../utils/utils';
class ProductDetails extends View {
    constructor(container) {
        super(container);
        const content = this.constructor.template.content.cloneNode(true);
        this.container.append(content);
        this._title = ensureElement('.card__title', this.container);
        this._image = ensureElement('.card__image', this.container);
        this._description = ensureElement('.card__text', this.container);
        this._category = ensureElement('.card__category', this.container);
        this._price = ensureElement('.card__price', this.container);
        this._button = ensureElement('.card__button', this.container);
        this._button.addEventListener('click', () => {
            this.emit('product:add', this.state);
        });
    }
    render(data) {
        this.state = data;
        this._title.textContent = data.title;
        this._image.src = data.image;
        this._image.alt = data.title;
        this._description.textContent = data.description;
        this._category.textContent = data.category;
        this._price.textContent = data.price ? `${data.price} синапсов` : 'Цена по запросу';
        this._category.className = `card__category card__category_${data.category.toLowerCase()}`;
    }
    get title() {
        return this._title.textContent || '';
    }
    set title(value) {
        this._title.textContent = value;
    }
    get description() {
        return this._description.textContent || '';
    }
    set description(value) {
        this._description.textContent = value;
    }
    get price() {
        const priceText = this._price.textContent;
        if (!priceText)
            return null;
        return parseInt(priceText.replace(/\D/g, ''));
    }
    set price(value) {
        this._price.textContent = value ? `${value} синапсов` : 'Цена по запросу';
    }
    get category() {
        return this._category.textContent || '';
    }
    set category(value) {
        this._category.textContent = value;
        this._category.className = `card__category card__category_${value.toLowerCase()}`;
    }
    get image() {
        return this._image.src;
    }
    set image(value) {
        this._image.src = value;
        this._image.alt = this.title;
    }
}
ProductDetails.template = ensureElement('#card-preview');
export { ProductDetails };
