import { View } from './view';
import { ensureElement } from '../../utils/utils';
class Modal extends View {
    constructor(container) {
        super(container);
        this._closeButton = ensureElement('.modal__close', this.container);
        this._content = ensureElement('.modal__content', this.container);
        this._closeButton.addEventListener('click', this.close.bind(this));
        this.container.addEventListener('click', this.handleOutsideClick.bind(this));
    }
    render(data) {
        this._content.innerHTML = '';
        this._content.append(data.content);
    }
    open() {
        this.container.classList.add('modal_active');
        this.emit('modal:open');
    }
    close() {
        this.container.classList.remove('modal_active');
        this.emit('modal:close');
        this._content.innerHTML = '';
    }
    handleOutsideClick(event) {
        if (event.target === this.container) {
            this.close();
        }
    }
}
Modal.template = ensureElement('#modal-container');
export { Modal };
