import { View } from './base/view';
import { ensureElement } from '../utils/utils';
class ProductCard extends View {
    constructor(container) {
        super(container);
        const content = this.constructor.template.content.cloneNode(true);
        const button = content.querySelector('.card');
        if (!button) {
            throw new Error('Card button not found in template');
        }
        this._title = ensureElement('.card__title', button);
        this._price = ensureElement('.card__price', button);
        this._category = ensureElement('.card__category', button);
        this._image = ensureElement('.card__image', button);
        button.addEventListener('click', () => {
            this.emit('card:select', this.state);
        });
        this.container.append(button);
    }
    render(data) {
        this.state = data;
        this.title = data.title;
        this.price = data.price;
        this.category = data.category;
        this.image = data.image;
    }
    get title() {
        return this._title.textContent || '';
    }
    set title(value) {
        this._title.textContent = value;
    }
    get price() {
        const priceText = this._price.textContent;
        if (!priceText)
            return null;
        return parseInt(priceText.replace(/\D/g, ''));
    }
    set price(value) {
        this._price.textContent = value ? `${value} синапсов` : 'Цена по запросу';
    }
    get category() {
        return this._category.textContent || '';
    }
    set category(value) {
        this._category.textContent = value;
        const categoryClasses = {
            'софт-скил': 'soft',
            'хард-скил': 'hard',
            'другое': 'other',
            'дополнительное': 'additional',
            'кнопка': 'button'
        };
        const categoryClass = categoryClasses[value.toLowerCase()] || value.toLowerCase();
        this._category.className = `card__category card__category_${categoryClass}`;
    }
    get image() {
        return this._image.src;
    }
    set image(value) {
        this._image.src = value;
        this._image.alt = this.title;
    }
}
ProductCard.template = ensureElement('#card-catalog');
export { ProductCard };
