import { View } from './base/view';
import { ensureElement } from '../utils/utils';
import { formatPhone } from '../utils/mask';
class ContactsForm extends View {
    constructor(container) {
        super(container);
        const content = this.constructor.template.content.cloneNode(true);
        this.container.append(content);
        this._emailInput = ensureElement('input[name="email"]', this.container);
        this._phoneInput = ensureElement('input[name="phone"]', this.container);
        this._submitButton = ensureElement('button[type="submit"]', this.container);
        this._emailInput.addEventListener('input', () => this.validateForm());
        this._phoneInput.addEventListener('input', () => {
            const value = this._phoneInput.value;
            this._phoneInput.value = formatPhone(value);
            this.validateForm();
        });
        this.container.addEventListener('submit', (e) => this.handleSubmit(e));
    }
    render(data) {
        if (data.email)
            this._emailInput.value = data.email;
        if (data.phone)
            this._phoneInput.value = data.phone;
        this.validateForm();
    }
    validateForm() {
        const email = this._emailInput.value.trim();
        const phone = this._phoneInput.value.trim();
        const isEmailValid = this.validateEmail(email);
        const isPhoneValid = this.validatePhone(phone);
        this._emailInput.classList.toggle('input_invalid', !isEmailValid && email.length > 0);
        this._phoneInput.classList.toggle('input_invalid', !isPhoneValid && phone.length > 0);
        const isValid = isEmailValid && isPhoneValid;
        this._submitButton.disabled = !isValid;
        this.emit('contacts:change', {
            email,
            phone,
        });
    }
    validateEmail(email) {
        return /^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(email);
    }
    validatePhone(phone) {
        return phone.length === 16;
    }
    handleSubmit(e) {
        e.preventDefault();
        this.emit('contacts:submit', {
            email: this._emailInput.value.trim(),
            phone: this._phoneInput.value.trim(),
        });
    }
    get email() {
        return this._emailInput.value;
    }
    set email(value) {
        this._emailInput.value = value;
        this.validateForm();
    }
    get phone() {
        return this._phoneInput.value;
    }
    set phone(value) {
        this._phoneInput.value = value;
        this.validateForm();
    }
    get isValid() {
        return this.validateEmail(this.email) && this.validatePhone(this.phone);
    }
}
ContactsForm.template = ensureElement('#contacts');
export { ContactsForm };
