import { View } from './base/view';
import { ensureElement } from '../utils/utils';
class Cart extends View {
    constructor(container) {
        super(container);
        const content = this.constructor.template.content.cloneNode(true);
        this.container.append(content);
        this._list = ensureElement('.basket__list', this.container);
        this._total = ensureElement('.basket__price', this.container);
        this._button = ensureElement('.basket__button', this.container);
        this._button.addEventListener('click', () => {
            this.emit('cart:checkout');
        });
    }
    calculateTotal(items) {
        return items.reduce((total, item) => total + item.price, 0);
    }
    saveCart() {
        localStorage.setItem('cart', JSON.stringify(this.state));
    }
    loadCart() {
        const saved = localStorage.getItem('cart');
        return saved ? JSON.parse(saved) : [];
    }
    render(items) {
        this.state = items;
        this._list.innerHTML = '';
        this.saveCart();
        this._button.disabled = items.length === 0;
        items.forEach((item, index) => {
            const itemElement = this.createCardItem(item, index + 1);
            this._list.append(itemElement);
        });
        const total = this.calculateTotal(items);
        this._total.textContent = `${total} синапсов`;
        localStorage.setItem('cart', JSON.stringify(items));
    }
    createCardItem(item, index) {
        const template = ensureElement('#card-basket');
        const element = template.content.cloneNode(true);
        ensureElement('.basket__item-index', element).textContent =
            String(index);
        ensureElement('.card__title', element).textContent =
            item.title;
        ensureElement('.card__price', element).textContent = `${item.price} синапсов`;
        const deleteButton = ensureElement('.card__button', element);
        deleteButton.addEventListener('click', () => {
            this.emit('cart:remove', item);
        });
        return element;
    }
    get items() {
        return this.state;
    }
    set items(value) {
        this.render(value);
    }
    get total() {
        return this.state.reduce((sum, item) => sum + item.price, 0);
    }
    get isEmpty() {
        return this.state.length === 0;
    }
}
Cart.template = ensureElement('#basket');
export { Cart };
